.about-container {
    padding: 5% 10%;
    text-align: left;
    background: linear-gradient(135deg, rgba(80, 0, 202, 0.05) 0%, rgba(16, 18, 59, 0.05) 100%);
}

.about-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.about-text {
    h1 {
        font-size: 2.5rem;
        margin-bottom: 2rem;
        background: linear-gradient(135deg, #5000ca 0%, #8b5cf6 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    .intro {
        font-size: 1.3rem;
        font-weight: 500;
        line-height: 1.8;
        margin-bottom: 1.5rem;
        color: rgba(255, 255, 255, 0.95);
    }

    p {
        font-size: 1.1rem;
        line-height: 1.8;
        margin-bottom: 1.5rem;
        color: rgba(255, 255, 255, 0.85);
        text-align: justify;
    }
}

.about-stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin-top: 2rem;

    @media (max-width: 768px) {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}

.stat-card {
    background: rgba(80, 0, 202, 0.1);
    border: 2px solid rgba(80, 0, 202, 0.3);
    border-radius: 15px;
    padding: 2rem;
    text-align: center;
    transition: all 0.3s ease;

    &:hover {
        transform: translateY(-5px);
        background: rgba(80, 0, 202, 0.15);
        border-color: rgba(80, 0, 202, 0.5);
        box-shadow: 0 10px 30px rgba(80, 0, 202, 0.3);
    }

    h2 {
        font-size: 3rem;
        font-weight: bold;
        margin: 0;
        background: linear-gradient(135deg, #5000ca 0%, #8b5cf6 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }

    p {
        font-size: 1.1rem;
        margin: 0.5rem 0 0 0;
        color: rgba(255, 255, 255, 0.8);
        font-weight: 500;
    }
}

// Light mode support
.light-mode {
    .about-container {
        background: linear-gradient(135deg, rgba(80, 0, 202, 0.03) 0%, rgba(240, 240, 255, 0.5) 100%);
    }

    .about-text {
        .intro {
            color: rgba(0, 0, 0, 0.95);
        }

        p {
            color: rgba(0, 0, 0, 0.85);
        }
    }

    .stat-card {
        background: rgba(80, 0, 202, 0.05);
        border-color: rgba(80, 0, 202, 0.2);

        &:hover {
            background: rgba(80, 0, 202, 0.1);
            border-color: rgba(80, 0, 202, 0.4);
            box-shadow: 0 10px 30px rgba(80, 0, 202, 0.15);
        }

        p {
            color: rgba(0, 0, 0, 0.8);
        }
    }
}

@media screen {
    @media (max-width: 768px) {
        .about-container {
            padding: 5% 5%;
        }

        .about-text {
            h1 {
                font-size: 2rem;
            }

            .intro {
                font-size: 1.15rem;
            }

            p {
                font-size: 1rem;
            }
        }

        .stat-card {
            h2 {
                font-size: 2.5rem;
            }

            p {
                font-size: 1rem;
            }
        }
    }
}